
.App {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 2rem;
  font-size: calc(10px + 2vmin);
  color: white;
}

.upper-container {
  display: flex;
  gap: 3rem;
}

.button-get {
  align-self: center;
}

.frame {
  margin: 1rem;
  border: 1px solid lightblue;
  border-radius: 8px;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

