form {
    border: 1px solid lightgoldenrodyellow;
    padding: 0 1rem;
    border-radius: 8px;
}

.h3-new {
    text-align: center;
    margin: 0.5rem 0;
}

.inputs {
    /* display: flex;
    justify-content: space-evenly; */
}

input {
    margin-right: 0.5rem;
}

.button-container {
    margin: 1rem 0;
    /* border: 1px solid red; */
    /* text-align: center; */
    display: flex;
    justify-content: center;
    align-items: center;
}

/* .button-post { */
button {
    width: 8rem;
    height: 2rem;
    background-color: lightgray;
    color: maroon;
    font-weight: bold;
    border-radius: 7px;
    border: 1px solid lightslategray;
}

/* .button-post:hover { */
button:hover {
    background-color: green;
    color: yellow;
    border: 1px solid green;
}